.post-list {
    list-style: none;
}

.post-list>li {
    background-color: #f0f5f9;
    margin-top: 10px;
    box-sizing: border-box;
    padding: 10px;
}

body.pillar main {
    background: #f0f5f9;
}

body.pillar article {
    background: #fff;
    box-sizing: border-box;
    padding: 50px;
}

body.pillar article p {
    font-size: 1.2em;
}